
import Vue from 'vue';
import XImg from 'vux/src/components/x-img/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
data() {
return {
account: '',
pass: '',
newPass: '',
confrimNewPass:''
};
},
components: {
XImg,
Icon,
XHeader
},













methods: {
change() {
if (!this.account)
return this.$vux.toast.show({
text: '请输入帐号',
type: 'text',
position: 'top'
});
if (!this.pass)
return this.$vux.toast.show({
text: '请输入原密码',
type: 'text',
position: 'top'
});
if (!this.newPass)
return this.$vux.toast.show({
text: '请输入新密码',
type: 'text',
position: 'top'
});
if (!this.confrimNewPass)
return this.$vux.toast.show({
text: '请再次输入新密码',
type: 'text',
position: 'top'
});
if(this.newPass != this.confrimNewPass)
{
return this.$vux.toast.show({
text: '两次输入的密码不一致',
type: 'text',
position: 'top'
});
}
Vue.http.post('puser/updatePwd', {
name: this.account,
password: this.pass,
rePassWord: this.newPass
})
.then(result => {
if (result.status) {
this.$router.push({
path: this.$route.query.toPath || '/'
});
} else {
this.$vux.toast.show({
text: result.message,
width: '18em',
type: 'text',
position: 'top'
});
}
});
}
}
};



